.header_header__2eggW {
  display: flex;
  position: relative;
  z-index: 3;
  flex-direction: row;
  background-color: red;
}
.header_header__2eggW > h3 {
  align-self: center;
}
.header_button__aSONM {
  position: relative;
  display: inline-block;
  margin: 20px;
}

.header_button__aSONM a {
  color: white;
  font-family: Helvetica, sans-serif;
  font-weight: bold;
  font-size: 15px;
  text-align: center;
  text-decoration: none;
  background-color: #ffa12b;
  display: block;
  position: relative;
  padding: 20px 40px;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-shadow: 0px 1px 0px #000;
  -webkit-filter: dropshadow(color=#000, offx=0px, offy=1px);
